
<template>
<div class="demo-input-suffix">
  <span class="title"></span>
  <el-date-picker
    :editable="false"
    v-model="startBudgetTime"
    :picker-options="pickerOptionsStart"
    size="mini"
    type="datetime"
    value-format="timestamp"
    format="yyyy-MM-dd HH:mm:ss"
    placeholder=""
    @change="changeEnd"/>-
  <el-date-picker
    :editable="false"
    v-model="endBudgetTime"
    :picker-options="pickerOptionsEnd"
    size="mini"
    type="datetime"
    value-format="timestamp"
    format="yyyy-MM-dd HH:mm:ss"
    default-time="['23:59:59']"
    placeholder=""
    @change="changeStart"/>
   <el-tooltip class="item" effect="dark" content="Top Left " placement="top-start">
      <el-button></el-button>
    </el-tooltip>
    <div class="info-area">1234565</div>
      <!--  -->
      <div ref="pdfContent" id="pdfContent">
        <div class="font">
          <span v-if="this.queryInfo.enable === '0'">-</span>
          <span v-else>-</span>
        </div>
        <el-table :data="revenueList" border stripe>
           <el-table-column label="UID" prop="uuid"></el-table-column>
          <el-table-column label="" prop="account"></el-table-column>
          <el-table-column label="" prop="identityid"></el-table-column>
          <el-table-column label="" prop="transactionTimes"></el-table-column>
          <el-table-column label="" prop="transactionTotal"></el-table-column>

        </el-table>
      </div>
</div>

</template>

<script>
import moment from 'moment'
export default {
  data () {
    return {
      // 
      pickerOptionsStart: {},
      pickerOptionsEnd: {},
      startBudgetTime: '', // 
      endBudgetTime: '' // 
    }
  },
  created () {
    this.timeee()
  },
  methods: {
    timeee () {
      console.log(moment.format())
    },
    // 
    changeStart () {
      if (!this.endBudgetTime) {
        this.pickerOptionsStart = {
          disabledDate: {}
        }
        return
      }
      this.pickerOptionsStart = Object.assign({}, this.pickerOptionsStart, {
        // this
        disabledDate: (time) => {
          var times = ''
          times = time.getTime() > this.endBudgetTime
          return times
        }
      })
    },
    //  
    changeEnd () {
      if (!this.startBudgetTime) {
        this.pickerOptionsEnd = {
          disabledDate: {}
        }
        return
      }
      this.pickerOptionsEnd = Object.assign({}, this.pickerOptionsEnd, {
        disabledDate: (time) => {
          return time.getTime() < this.startBudgetTime
        }
      })
    }
  }
}
</script>

