<template>
  <div>
    <TopBreadcrumb :titles="['CMS', 'banner']"></TopBreadcrumb>
    <el-button type="info" @click="addDialogVisible = true" class="el-icon-plus" size="min"></el-button>&nbsp;
          <!--  -->
                        <el-select  v-model="enable.value" placeholder=""  clearable
                         style="width:10% ">
                            <el-option
                                v-for="(enableValue,index) in enable"
                                :key="index"
                                v-bind:label="enableValue.label"
                                v-bind:value="enableValue.value"
                            >{{enableValue.label}}</el-option>
                        </el-select>&nbsp;
                         <el-button type="primary" @click="clear"></el-button>
        <el-button type="primary" @click="Search"></el-button>
    <el-card>
      <!--  -->
      <el-table :data="bannerList" stripe border>
        <el-table-column label="ID" prop="id" width="50px"></el-table-column>
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <img :src="scope.row.imageURL"  class="imgwidth"/>
          </template>
        </el-table-column>
        <el-table-column label="" prop="type">
          <template slot-scope="scope">
            <div v-if="scope.row.type === 1">WEB</div>
            <div v-else>APP</div>
          </template>
        </el-table-column>
        <el-table-column label="" prop="lang">
          <template slot-scope="scope">
            <span v-if="scope.row.lang ==='el_GR'"></span>
            <span v-else-if="scope.row.lang ==='zh_CN'"></span>
            <span v-else>Engilsh</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="classify">
          <template slot-scope="scope">
            <span v-if="scope.row.classify ===1">BANNER</span>
             <span v-else-if="scope.row.classify ===2">FOOTER</span>
            <span v-else>FaQ</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="sort"></el-table-column>
        <el-table-column label="" width="180px">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.$index, scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUserById(scope.$index, scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="80%" @close="addDialogClosed">
        <el-form :model="addForm" :rules="rules" ref="addForm" label-width="100px">
          <el-form-item label=":" prop="title">
            <el-input v-model="addForm.title"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="type">
            <el-radio-group v-model="addForm.type">
              <el-radio label="1">WEB</el-radio>
              <el-radio label="2">APP</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" prop="classify">
            <el-radio-group v-model="addForm.classify">
              <el-radio label="1">BANNER</el-radio>
              <el-radio label="2">FOOTER</el-radio>
              <el-radio label="3">FaQ</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" prop="lang">
            <el-radio-group v-model="addForm.lang">
              <el-radio label="el_GR"></el-radio>
              <el-radio label="zh_CN"></el-radio>
              <el-radio label="en_US">Engilsh</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" prop="sort">
            <el-input-number v-model="addForm.sort"  :min="1" :max="5" label=""></el-input-number>
          </el-form-item>

          <el-form-item label=":" prop="publicPic">
            <template>
              <el-upload
                class="upload-demo"
                action="string"
                :auto-upload="false"
                :limit="1"
                ref="upload"
                :http-request="addBanner"
                name="publicPic"
                multiple
              >
                <el-button size="small" type="primary"></el-button>
              </el-upload>
            </template>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addBanner"></el-button>
        </span>
      </el-dialog>

      <!-- -->
      <el-dialog title="" :visible.sync="editDialogVisible" width="70%" @close="editDialogClosed">
        <el-form :model="editForm" ref="editForm" label-width="100px" :rules="rules">
          <el-form-item label="" prop="title">
            <el-input v-model="editForm.title"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="type">
            <el-radio-group v-model="editForm.type">
              <el-radio :label="1">WEB</el-radio>
              <el-radio :label="2">APP</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" prop="classify">
            <el-radio-group v-model="editForm.classify">
              <el-radio :label="1">BANNER</el-radio>
              <el-radio :label="2">FOOTER</el-radio>
               <el-radio :label="3">FaQ</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" prop="lang">
            <el-radio-group v-model="editForm.lang">
              <el-radio label="el_GR"></el-radio>
              <el-radio label="zh_CN"></el-radio>
              <el-radio label="en_US">Engilsh</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" prop="sort">
            <el-input-number v-model="editForm.sort" :min="1" :max="5" label=""></el-input-number>
          </el-form-item>
          <!--      <el-form-item label="" >
          <template slot-scope="scope">
            <img :src="scope.row.image" style="max-width:50%" />
          </template>
          </el-form-item>-->
          <el-form-item label=":" >
            <img :src="editForm.imageURL" style="max-width:100%" />
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveEdit"></el-button>
        </span>
      </el-dialog>
      <!-- -->
      <el-dialog :visible.sync="delDialogVisible" width="20%" @close="delDialogClosed">
        <p class="deltxt">?</p>
        <span slot="footer" class="dialog-footer">
          <el-button @click="delDialogVisible = false"> </el-button>
          <el-button type="primary" @click="savedel"></el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { getcmslist, editban, delban, uploadban } from '../../api/index.js'

export default {
  data () {
    return {
      fileList: [],
      name: 'upload',
      queryInfo: {
        pagenum: 1,
        pagesize: 10,
        date: []
      },

      bannerList: [],
      total: 0,

      editForm: {},
      addForm: {
        title: '',
        type: '',
        lang: '',
        classify: '',
        sort: ''
      },
      enable: [

        {
          label: '',
          value: 'el_GR'
        },
        {
          label: '',
          value: 'zh_CN'
        },
        {
          label: 'Engilsh',
          value: 'en_US'
        }
      ],

      editDialogVisible: false,
      addDialogVisible: false,
      delDialogVisible: false,
      formData: [],
      rules: {
        title: [
          { required: true, trigger: 'blur' }
        ],
        type: [
          { required: true, trigger: 'blur' }
        ],
        lang: [
          { required: true, trigger: 'blur' }
        ],
        classify: [
          { required: true, trigger: 'blur' }
        ],
        sort: [
          { required: true, trigger: 'blur' }
        ],
        publicPic: [
          { required: true, trigger: 'blur' }
        ]
      }
    }
  },
  /*   components: {
    OldUploadFile
  }, */
  created () {
    this.getbannerList()
    this.objList()
  },

  methods: {
    objList () {
      this.objname = localStorage.getItem('mg_name')
      this.objpwd = localStorage.getItem('mg_pwd')
      console.log(typeof this.objname, this.objpwd)

      if (this.objname == null || this.objpwd == null) {
        console.log('15132321')
        this.$router.push('/login')
      }
    },
    async getbannerList () {
      let data = {
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        paginate: this.queryInfo.pagesize,
        page: this.queryInfo.pagenum,
        type: '',
        lang: this.enable.value,
        classify: ''
      }
      await getcmslist(data).then(res => {
        this.bannerList = res.data
        this.total = res.pagination.total_record
        console.log('12323', this.bannerList)
      })
    },

    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.queryInfo.pagenum = 1
      this.getbannerList()
    },

    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getbannerList()
    },

    /* banner */
    addDialogClosed () {
      this.$refs.addForm.resetFields()
    },
    async  addBanner () {
      let _this = this
      this.addDialogVisible = false
      let reader = new FileReader()
      reader.readAsDataURL(this.$refs.upload.uploadFiles[0].raw)

      var data = {
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        title: this.addForm.title,
        lang: this.addForm.lang,
        type: this.addForm.type.toString(),
        classify: this.addForm.classify.toString(),
        sort: this.addForm.sort
      }
      reader.onload = function (e) {
        var imgFile = e.target.result
        data.publicPic = imgFile
        uploadban(data).then(res => {
          if (res.error_code === 0) {
            _this.$alert('', '', {
              confirmButtonText: 'OK',
              callback: action => {
                _this.$message({
                  type: 'success',
                  message: ''
                })
              }
            })
            _this.getbannerList()
          } else {
            _this.$message.error('')
          }
        })
      }
    },

    onChangeFileFun (obj) {
      this.uploadName = obj.name
      this[`${this.uploadName}Loading`] = true
    },

    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },

    showEditDialog (index, row) {
      this.editForm = row
      this.editDialogVisible = true

      this.getbannerList()
    },
    async saveEdit () {
      this.editDialogVisible = false

      var data = {
        id: this.editForm.id,
        image: this.editForm.image,
        lang: this.editForm.lang,
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        title: this.editForm.title,
        type: this.editForm.type,
        classify: this.editForm.classify,
        sort: this.editForm.sort
      }
      await editban(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
        this.getbannerList()
      })
    },
    editDialogClosed () {
      this.$refs.editForm.resetFields()
    },
    deleteUserById (index, row) {
      this.deldata = row
      this.delDialogVisible = true

      this.getbannerList()
    },
    async savedel () {
      this.delDialogVisible = false
      var data = {
        id: this.deldata.id,
        image: this.deldata.image,
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state')
      }
      await delban(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
        this.getbannerList()
      })
    },
    delDialogClosed () {
      this.delDialogVisible = false
    },
    async clear () {
      this.enable.value = ''
    },
    async Search () {
      this.queryInfo.pagenum = 1
      this.getbannerList()
    }
  }
}
</script>

<style lang="less" scoped>
.text {
  display: inline-block;
  margin: 0 5px;
  font-size: 12px;
}
table,
th,
td {
  border: 1px solid #dddddd;
  border-collapse: collapse;
  padding: 10px;
}
.deltxt {
  text-align: center;
  font-family: "Noto Sans TC", sans-serif;
  font-size: 24px;
}
.imgwidth{
   max-width:60%;
   margin:  auto;
  display: block;
}
</style>
