<el-form ref="form" :model="form" label-width="10%">
  <el-form-item label=":">
    {{this.$route.query.username}}
  </el-form-item>
  <el-form-item label=":">
<div style="color:brown" v-if="this.$route.query.auth_status == '0'"></div>
<div style="color:green" v-else-if="this.$route.query.auth_status  == '1'" ></div>
<div v-else style="color:red"></div>
       <!--  <div style="color:green" v-else></div> -->
  </el-form-item>
 <el-form-item label=":">
        <div v-if="this.$route.query.certificate_type == '1'"></div>
        <div v-else-if="this.$route.query.certificate_type == '2'"></div>
        <div v-else></div>
  </el-form-item>
   <el-form-item label=":">
<img :src="this.$route.query.first_photo" style="max-width:50%"/>
  </el-form-item>
     <el-form-item label=":">
<img :src="this.$route.query.second_photo" style="max-width:50%"/>
  </el-form-item>
     <el-form-item label=":">
 <img :src="this.$route.query.third_photo" style="max-width:50%"/>
  </el-form-item>
       <el-form-item label=":">
 <img :src="this.$route.query.fourth_photo" style="max-width:50%"/>
  </el-form-item>
  <el-form-item label="" v-if="this.$route.query.auth_status===0" required>
    <el-input type="textarea" v-model="form.desc"></el-input>
  </el-form-item>
    <el-form-item  v-else-if="this.$route.query.auth_status===1" label="">
        {{this.$route.query.reason}}
  </el-form-item>
      <el-form-item  v-else label="" style="color:red">
          {{this.$route.query.reason}}
  </el-form-item>
  <el-form-item style="text-align:center">
    <el-button  @click="uppage"></el-button>&nbsp;&nbsp;
    <span v-if="this.$route.query.auth_status===0">
    <el-button  @click="Submitunpass"  type="danger" :disabled="fail"></el-button>
    <el-button type="primary" @click.once="Submitpass" :disabled="Success"></el-button>
    </span>
        <span v-else>
    </span>
  </el-form-item>
</el-form>

    </el-card>
  </div>
</template>

<script>
import { KycSuccess, KycFail, getKYCcertificateBumber } from '../../api/index.js'
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
        enable: '',
        date: []
      },
      fail: false,
      Success: false,
      getdata: '',
      total: 0,
      form: {
        username: '',
        desc: ''
      },
      searchlist: '',
      /*  editDialogVisible: false, */
      editForm: {},
      realname: {},
      nameList: [],
      checkidlist: {}
    }
  },
  created () {
    console.log('1223', this.$route.query)
    this.objList()
    this.checkid()
  },

  methods: {
    async  checkid () {
      var data = {
        uuid: this.$route.query.uuid,
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state')
      }
      await getKYCcertificateBumber(data).then(res => {
        this.checkidlist = res.data
        console.log('check', this.checkidlist)
      })
    },

    objList () {
      this.objname = localStorage.getItem('mg_name')
      this.objpwd = localStorage.getItem('mg_pwd')
      console.log(typeof this.objname, this.objpwd)

      if (this.objname == null || this.objpwd == null) {
        console.log('15132321')
        this.$router.push('/login')
      }
    },
    /*  */
    async Submitunpass () {
      var data = {
        uuid: this.$route.query.uuid,
        reason: this.form.desc,
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state')
      }
      await KycFail(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
          this.fail = false
          this.$router.push('/realname')
        } else {
          this.$message.error(',')
        }
      })
    },
    uppage () {
      this.$router.push('/realname')
    },
    /*  */
    async Submitpass () {
      let data = {
        uuid: this.$route.query.uuid,
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        reason: this.form.desc
      }
      await KycSuccess(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
          this.Success = true
          this.$router.push('/realname')
        } else {
          this.$message.error('')
        }
      })
    }

  }
}
</script>

