<template>
  <div>
    <TopBreadcrumb :titles="['', '']"></TopBreadcrumb>

    <el-card>
      <!--  -->
      <div  class="text">
      <el-input v-model="searchlist" @keyup.enter.native="Search" style="width:30%" placeholder="">

      </el-input>&nbsp;
        <el-button type="primary" @click="addDialogVisible = true"></el-button>
        <el-button type="primary" @click="Search" ></el-button>
      </div>
      <!--  -->
      <el-table :data="bankList" stripe border>
        <el-table-column label="ID" prop="id"></el-table-column>
        <el-table-column label="" prop="bankcode" ></el-table-column>
        <el-table-column label="" prop="bank_ch"></el-table-column>
        <el-table-column label="" prop="bank_en"></el-table-column>

        <el-table-column label="">
              <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.status === 0"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
 <!--          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#BEBEBE"
              :active-value="1"
              :inactive-value="0"
              @change="changeSwitch(scope.$index,scope.row)"
            ></el-switch>
          </template> -->
        </el-table-column>
        <el-table-column label="" width="180px">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.$index, scope.row)"
            ></el-button>
            <!--     <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUserById(scope.row.id)"
            ></el-button>-->
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
          <el-form-item label="" prop="addbankcode">
          <el-input v-model="addForm.addbankcode"  placeholder="ex:809"></el-input>
        </el-form-item>
        <el-form-item label="" prop="addbankch">
          <el-input v-model="addForm.addbankch"  placeholder="ex:"></el-input>
        </el-form-item>
                <el-form-item label="" prop="addbanken">
          <el-input v-model="addForm.addbanken"  placeholder="ex:KGI Bank"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addBank"></el-button>
      </span>
    </el-dialog>

    <!-- -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" ref="editFormRef" label-width="100px">
        <el-form-item label="">
          <el-input v-model="editForm.bank_ch" ></el-input>
        </el-form-item>
     <el-form-item label="">
          <el-input v-model="editForm.bank_en" ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.bankcode" ></el-input>
        </el-form-item>
           <el-form-item label="">
                        <el-select  v-model="editForm.status" placeholder="">
                            <el-option
                                v-for="(enableValue,index) in enable"
                                :key="index"
                                v-bind:label="enableValue.label"
                                v-bind:value="enableValue.value"
                            >{{enableValue.label}}</el-option>
                        </el-select>
        </el-form-item>
   <!--        <el-form-item label="">
           <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#BEBEBE"
              :active-value="1"
              :inactive-value="0"

            ></el-switch>
        </el-form-item> -->
        </el-form >
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveEdit"></el-button>
      </span>

    </el-dialog>
  </div>
</template>

<script>
import { editbankData, bankdata, createBank } from '../../api/index.js'

export default {
  data () {
    return {
      searchlist: '',
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
        enable: ''
      },
      bankList: [],

      total: 0, // 

      addDialogVisible: false,
      addForm: {
        addbankch: '',
        addbanken: '',
        addbankcode: ''
      },
      addFormRules: {
        addbanken: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 12, message: ' 3  12 ', trigger: 'blur' }
        ],
        addbankcode: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        addbankch: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },

      editDialogVisible: false,
      table: {},
      editForm: {},
      /*       editFormRules: {
        editcharge: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      } */
      enable: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        }
      ]

    }
  },

  created () {
    this.getbankList()
    this.objList()
  },

  methods: {
    objList () {
      this.objname = localStorage.getItem('mg_name')
      this.objpwd = localStorage.getItem('mg_pwd')
      console.log(typeof this.objname, this.objpwd)

      if (this.objname == null || this.objpwd == null) {
        console.log('15132321')
        this.$router.push('/login')
      }
    },
    async changeSwitch (index, row) {
      let data = {
        bank_en: this.editForm.bank_en,
        bank_ch: this.editForm.bank_ch,
        bankcode: this.editForm.bankcode,
        status: this.scope.row.status,
        id: this.editForm.id
      }
      await editbankData(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
        this.getbankList()
      })
    },
    // 
    async getbankList () {
      let data = {
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        paginate: this.queryInfo.pagesize,
        page: this.queryInfo.pagenum,
        searchValue: this.searchlist
      }
      await bankdata(data).then(res => {
        this.bankList = res.data
        this.total = res.pagination.total_record
      })
    },

    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.queryInfo.pagenum = 1
      this.getbankList()
    },

    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getbankList()
    },

    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },

    showEditDialog (index, row) {
      this.editForm = row
      this.editDialogVisible = true
    },

    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },

    async saveEdit () {
      this.editDialogVisible = false
      var data = {
        bank_en: this.editForm.bank_en,
        bank_ch: this.editForm.bank_ch,
        bankcode: this.editForm.bankcode,
        id: this.editForm.id,
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        status: this.editForm.status.toString()
      }
      await editbankData(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
        this.getbankList()
      })
    },
    /*
         async deleteUserById (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getChargeList()
    },  */

    async addBank () {
      this.addDialogVisible = false
      var data = {
        bank_en: this.addForm.addbanken,
        bank_ch: this.addForm.addbankch,
        bankcode: this.addForm.addbankcode,
        status: 1,
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state')
      }

      await createBank(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
        this.getbankList()
      })
    },
    async Search () {
      this.queryInfo.pagenum = 1
      await this.getbankList()
    }
  }
}
</script>

