<template>
  <div>
    <TopBreadcrumb :titles="['', '']"></TopBreadcrumb>

    <el-card>
      <!--  -->
      <div style="vertical-align: middle">
        <el-input v-model="searchlist" @keyup.enter.native="Search" style="width:30%" placeholder="">
        </el-input>
        <el-button type="primary" @click="addDialogVisible = true"></el-button>
        <el-button type="primary" @click="Search" ></el-button>
      </div>
      <!--  -->
      <el-table :data="chargeList" stripe border>
        <el-table-column label="ID" prop="id"></el-table-column>
        <el-table-column label="" prop="bank_en"></el-table-column>
        <el-table-column label="" prop="rate"></el-table-column>
        <el-table-column label="" prop="txt_rate"></el-table-column>
        <el-table-column label="" prop="trans_type">
          <template slot-scope="scope">
            <span v-if="scope.row.trans_type === 0"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column label="" >
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.status === 0"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
<!--           <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#BEBEBE"
              :active-value="1"
              :inactive-value="0"
              active-text=""
              inactive-text=""

              @change="changeSwitch(scope.$index,scope.row)"
            ></el-switch>
          </template> -->
        </el-table-column>
        <el-table-column label="" width="180px">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.$index, scope.row)"
            ></el-button>
            <!--     <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUserById(scope.row.id)"
            ></el-button>-->
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item label="">
          <!--  <el-input v-model="addForm.addbank"></el-input> -->
          <el-select v-model="addForm.addbank" placeholder="">
            <el-option
              v-for="item in bankList"
              :key="item.id"
              :label="item.bank_ch+item.bank_en"
              :value="item.bank_en"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="addCharge">
          <el-input v-model="addForm.addCharge"></el-input>
        </el-form-item>
              <el-form-item label="" prop="addCharge">
          <el-input v-model="addForm.addtxtrate"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCharge"></el-button>
      </span>
    </el-dialog>

    <!-- -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" ref="editFormRef" label-width="100px">
        <el-form-item label="">
          <el-input v-model="editForm.bank_en" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-input v-model="editForm.rate"></el-input>
        </el-form-item>
          <el-form-item label="" >
          <el-input v-model="editForm.txt_rate"></el-input>
        </el-form-item>
          <el-form-item label="">
                        <el-select  v-model="editForm.trans_type" placeholder="" value-key="id">
                            <el-option
                                v-for="(enableValue) in enable"
                                :key="enableValue.value"
                                :label="enableValue.label"
                                :value="enableValue.value"
                            >{{enableValue.label}}</el-option>
                        </el-select>
        </el-form-item>
             <el-form-item label="">
                         <el-select  v-model="editForm.status" placeholder="">
                             <el-option
                                v-for="(statuslistValue,index) in statuslist"
                                :key="index"
                                :label="statuslistValue.label"
                                :value="statuslistValue.value"
                            >{{statuslistValue.label}}</el-option>

                        </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveEdit()"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  chargeData,
  createCharge,
  editchargedata,

  chargebankList
} from '../../api/index.js'

export default {
  data () {
    return {
      query: {},
      searchlist: '',
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
        enable: ''
      },
      chargeList: [],
      bankList: [],
      total: 0,

      addDialogVisible: false,
      addForm: {
        addbank: '',
        addCharge: '',
        addtxtrate: ''
      },
      addFormRules: {
        addbank: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        addCharge: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },

      editDialogVisible: false,
      table: {},
      editForm: {

      },
      enable: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        }

      ],
      statuslist: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        }

      ]
    }
  },

  created () {
    this.getChargeList()
    this.getBankList()
    this.objList()
  },

  methods: {
    objList () {
      this.objname = localStorage.getItem('mg_name')
      this.objpwd = localStorage.getItem('mg_pwd')
      console.log(typeof this.objname, this.objpwd)

      if (this.objname == null || this.objpwd == null) {
        console.log('15132321')
        this.$router.push('/login')
      }
    },
    // 
    async getBankList () {
      let data = {
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state')
      }
      await chargebankList(data).then(res => {
        this.bankList = res.data
      })
    },

    // 
    async getChargeList () {
      let data = {
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        paginate: this.queryInfo.pagesize,
        page: this.queryInfo.pagenum,
        searchValue: this.searchlist
      }

      await chargeData(data).then(res => {
        this.chargeList = res.data
        this.total = res.pagination.total_record
      })
    },

    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.queryInfo.pagenum = 1
      this.getChargeList()
    },

    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getChargeList()
    },

    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },

    showEditDialog (index, row) {
      this.editForm = row

      this.editDialogVisible = true
    },

    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },

    async saveEdit () {
      this.editDialogVisible = false
      let data = {
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        bank_en: this.editForm.bank_en,
        rate: this.editForm.rate,
        txt_rate: this.editForm.txt_rate,
        trans_type: this.editForm.trans_type.toString(),
        status: this.editForm.status.toString(),
        id: this.editForm.id
      }
      await editchargedata(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
        this.getChargeList()
      })
    },
    /*
         async deleteUserById (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getChargeList()
    },  */

    async addCharge () {
      this.addDialogVisible = false
      var data = {
        mg_name: localStorage.getItem('mg_name'),
        mg_pwd: localStorage.getItem('mg_pwd'),
        mg_state: localStorage.getItem('mg_state'),
        bank_en: this.addForm.addbank,
        rate: this.addForm.addCharge,
        txt_rate: this.addForm.addtxtrate
      }

      await createCharge(data).then(res => {
        if (res.error_code === 0) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
        this.getChargeList()
      })
    },
    async Search () {
      this.queryInfo.pagenum = 1
      await this.getChargeList()
    }
  }
}
</script>

<style>
</style>
